.tooltip {
    top: 0;
    left: 0;
    width: 240px;
    position: absolute;
    background: #fefefe;
    border-radius: 8px;
    padding: 16px;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.1);
    z-index: 100;
    margin: 40px;
}

.tooltip h4 {
    margin: 0 0 8px 0;
}

.tooltip p {
    margin: 0 0 8px 0;
}
